.nav__elem--first .litle-text {
  color: #95a0a3;
  font-size: 12px;
  margin-top: 11px;
}

.nav__elem--second {
  padding: 43px;
  display: inline-block;
}

@media only screen and (max-width: 1200px) {
  .nav__elem--second {
    padding: 20px;
  }
}

@media only screen and (max-width: 995px) {
  .nav__elem--second {
    padding: 0;
  }
}

.nav__elem--second .menu {
  font-size: 15px;
  text-align: center;
}

@media only screen and (max-width: 995px) {
  .nav__elem--second .menu {
    display: none;
  }
}

.nav__elem--second .menu li {
  display: inline;
  margin-left: 42px;
  width: 20px;
  position: relative;
  cursor: pointer;
}

.nav__elem--second .menu li:first-child {
  margin-left: 20px;
}

.nav__elem--second .menu li a {
  color: #6f7b80;
}

@media only screen and (max-width: 995px) {
  .nav__elem--second .menu li a {
    display: block;
  }
}

.nav__elem--second .menu li::before {
  position: absolute;
  content: '';
  bottom: -8px;
  width: 100%;
  height: 2px;
  background: #ffbb1c;
  opacity: 0;
  transition: opacity 0.5s, transform 0.5s;
}

.nav__elem--second .menu li::before {
  left: 0;
  transform: translateX(-100%);
}

.nav__elem--second .menu li:active::before, .nav__elem--second .menu li:hover::before {
  transform: translate(0);
  opacity: 1;
}

@media only screen and (max-width: 995px) {
  .nav__elem--second .menu-expanded {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 0;
    background: blue;
    top: 0;
    right: 0;
    overflow: scroll;
    transition: all .25s;
  }
}

.nav__elem--second .menu-collapsed {
  display: none;
}

