  .nav__elem--second .menu-collapsed {
    display: block;
    width: 50px;
    height: 50px;
    background: red;
    position: fixed;
    bottom: 10px;
    right: 10px;
    z-index: 99;
    border-radius: 50px;
  }
  .nav__elem--second .menu-collapsed .bar {
    position: fixed;
    transition: all .25s;
    content: "";
    bottom: 35px;
    right: 15px;
    height: 3px;
    width: 40px;
    background-color: #f1f1f1;
  }
  .nav__elem--second .menu-collapsed .bar::before, .nav__elem--second .menu-collapsed .bar::after {
